@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

@import "../../Styles/variable";

/*top bar*/
header {
  background: $mainColor;
  box-shadow: 0px 8px 25px rgb(0 0 0 / 10%);
}
.topbar {
  padding: 7px 0px;
}
.top_bar_btn {
  margin-top: 18px;
}

#custom-search-input {
  margin: 10px 0px 0px 0px;
  padding: 0;
  width: 500px;
}

#custom-search-input .search-query {
  width: 100%;
  padding: 0px 12px;
  margin-bottom: 0;
  background: $mainColor;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  height: 46px;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
}
#custom-search-input .input-group-btn img {
  padding: 4px;
}

#custom-search-input .search-query::-webkit-input-placeholder {
  color: #bdbdbd;
}

#custom-search-input button {
  border: 0;
  background: none;
  padding: 2px 5px;
  margin-top: 2px;
  position: absolute;
  right: 0;
  top:0px;
  margin-bottom: 0;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  cursor: unset;
  z-index: 999;
}

.btn.btn-bg-black{
  background: #000000;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: $mainColor;
  transition: 0.25s ease;
  padding: 9px 23px;
}
.btn.btn-bg-black:hover{
  background: $primaryColor;
}

.login_head{
  box-shadow: none;
}
.login_head .middle_nav_login {
  background: $navbgColor;
  margin: 10px 0px 30px;
}
.login_head .middle_nav_login .navbar-nav .nav-item {
  margin: 0px 26px 0px;
}
.login_head .middle_nav_login .navbar-nav .nav-item .nav-link{
  font-family: Sora medium;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: $mainColor;
}

.other_head .btn.btn-bg-pink {
  background: #ec498a;
  border: 1px solid #ec498a;
  box-sizing: border-box;
  border-radius: 10px;
  height: 40px;
  padding: 8px 21px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  transition: 0.25s ease;
}
.other_head .btn.btn-bg-pink:hover {
  background: #fff;
  color: #ec498a;
}
.other_head .MuiButton-text.MuiButton-root {
  background: #fff;
  border: 1px solid #000000;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  height: 38px;
  padding: 8px 21px;
  transition: 0.25s ease;
}
.other_head .MuiButton-text.MuiButton-root:hover {
  background: #ec498a;
  color: #fff !important;
}

/*Middle navbar*/
.other_head .middle_nav_login .navbar-nav .nav-item {
  margin: 10px 16px 0px 10px;
}
.other_head .middle_nav_login .navbar-nav .nav-item .nav-link {
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  color: #d62c79;
  text-transform: capitalize;
  padding: 7px 13px;
  transition: 0.25s ease;
}
.other_head .middle_nav_login .navbar-nav .nav-item .nav-link:hover {
  background: #ec498a;
  color: #fff;
}
.other_head .middle_nav_login .navbar-nav .nav-item.active {
  background: #ec498a;
}
.other_head .middle_nav_login .navbar-nav .nav-item.active .nav-link {
  color: #fff;
}

